export default {
	data() {
		return {
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			hidden: true,
			showDelMask: false,
			listCurID: '',
			topList: [{
					name: '新的朋友',
					img: '/static/lib/newfriend.jpg',
					url: "/pages/index/addNewFriend/addNewFriend",
				},
				{
					name: '群聊',
					img: '/static/lib/groupchat.jpg',
				},
				{
					name: '标签',
					img: '/static/lib/sign.jpg',
					url: '/pages/dataList/dataList',
				},
				{
					name: '公众号',
					img: '/static/lib/publicnum.jpg',
				},
				{
					name: '企业微信联系人',
					img: '/static/lib/team.jpg',
				}
			],
			listCur: '',

			list: [],
			wordlist: []
		};
	},
	onLoad() {
		let list = [{}];
		let	arr =[{
				name: "↑"
			},
			{
				name: "☆"
			}],
	obj3 = {
		name: "#"
	};


	
	for (let i = 0; i < 26; i++) {
		list[i] = {};
		list[i].name = String.fromCharCode(65 + i);

	}
	list = [...arr,...list]
	list = [...list, obj3]
	this.wordlist = list;
},
onShow() {
		this.getListData()
	},
	onReady() {
		let that = this;
		uni.createSelectorQuery().select('.indexBar-box').boundingClientRect(function(res) {
			that.boxTop = res.top
		}).exec();
		uni.createSelectorQuery().select('.indexes').boundingClientRect(function(res) {
			that.barTop = res.top
		}).exec()
	},
	methods: {
		getListData() {
			this.$util.get('/wechatUser/findWechatFriendPage').then(res => {
				this.list = res.data.data
			})
		},
		//获取文字信息
		getCur(e) {
			this.hidden = false;
			this.listCur = this.list[e.target.id].name;
		},
		onshowNext(url) {
			uni.navigateTo({
				url: url
			})
		},
		setCur(e) {
			this.hidden = true;
			this.listCur = this.listCur
		},
		//滑动选择Item
		tMove(e) {
			let y = e.touches[0].clientY,
				offsettop = this.boxTop,
				that = this;
			//判断选择区域,只有在选择区才会生效
			if (y > offsettop) {
				let num = parseInt((y - offsettop) / 20);
				this.listCur = that.list[num].name
			};
		},

		//触发全部开始选择
		tStart() {
			this.hidden = false
		},

		//触发结束选择
		tEnd() {
			this.hidden = true;
			this.listCurID = this.listCur
		},
		indexSelect(e) {
			let that = this;
			let barHeight = this.barHeight;
			let list = this.list;
			let scrollY = Math.ceil(list.length * e.detail.y / barHeight);
			for (let i = 0; i < list.length; i++) {
				if (scrollY < i + 1) {
					that.listCur = list[i].name;
					that.movableY = i * 20
					return false
				}
			}
		},
		onLongPress(e, item) {
			this.listItemValue = item
			this.showDelMask = true
		},
		colseMask() {
			setTimeout(() => {
				this.showDelMask = false
			}, 1000)
		},
		onDelList() {
			this.$util.get('/wechatUser/delWcFriend?wcFriendId=' + this.listItemValue.id).then(res => {
				if (res.data.code == 200) {
					this.showDelMask = false
					this.getListData()
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: "none",
					});
				}
			})
		},




	}
}
